* {
    box-sizing: border-box;
    font-family: "Arial";
    font-size: 20px;
}

#root {
    overflow: scroll;
    height: 100vh;
}

.App {
    width: 100%;
    min-width: 320px;
    max-width: 700px;
    margin: 100px auto;
    padding: 50px;
    background-color: #039BE5;
    border-radius: 5px;
    box-shadow: 5px 5px 10px 2px rgba(0, 0, 0, 0.3);
}

@media (max-width: 678px) {
    .App {
        padding: 30px;
        margin: 0;
        min-width: 100%;
        max-width: 100%;
        width: 100%;
        min-height: 100vh;
        padding: 20px;
        border-radius: 0;
        box-shadow: none;
    }
}

@media (max-width: 400px) {
    .App {
        padding: 5px;
    }
}
