.TodoItem {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    align-items: center;
    background-color: #0288D1;
    color: #fff;
    min-height: 50px;
    margin-bottom: 5px;
    border-radius: 5px;
    padding: 10px 20px;
    overflow-wrap: break-word;
}

.TodoItem__value {
    width: 100%;
    min-width: 0;
    cursor: pointer;

}

.TodoItem__icon {
    width: 15px;
    height: 15px;
}

.TodoItem__controls {
    flex-shrink: 0;
}

.TodoItem__input {
    display: block;
    border: none;
    outline: none;
    border-radius: 5px;
    width: 100%;
}

.TodoItem__button {
    border: none;
    outline: none;
    cursor: pointer;
}

.TodoItem--edit {
    background-color: #ffce2c;
}

.TodoItem--edit:hover {
    background-color: #fff235;
}

.TodoItem--trash {
    margin-left: 5px;
    background-color: #ff3c3c;
}

.TodoItem--trash:hover {
    background-color: #ff7664;
}

.TodoItem--done .TodoItem__value {
    text-decoration: line-through;
}

.TodoItem__button--edit {
    background-color: #ffffff;
}


@media (max-width: 400px) {
    .TodoItem__value  {
        font-size: 16px;
    }

    .TodoItem__input {
        font-size: 16px;
        min-height: 100%;
        height: 100%;
    }
}
