.TodoAdd {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    height: 40px
}

.TodoAdd__input {
    width: 100%;
    border-radius: 5px;
    border: none;
    outline: none;
    padding: 10px;
}

.TodoAdd__button {
    border-radius: 5px;
    border: none;
    outline: none;
    font-size: 13px;
    padding: 10px;
    background-color: #0288D1;
    color: #fff;
    cursor: pointer;
}

.TodoAdd__button:hover {
    background-color: #6eccff;
}

@media (max-width: 400px) {
    .TodoAdd {
        flex-wrap: wrap;
        height: auto;
    }

    .TodoAdd__input {
        font-size: 16px;
    }

    .TodoAdd__button {
        width: 100%;
        padding: 10px;
        font-size: 16px;
    }
}
